// Preloader
.preloader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: 99999;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    justify-content: center;
}
.cssload-thecube {
    width: 73px;
    height: 73px;
    margin: 0 auto;
    margin-top: 49px;
    position: relative;
    transform: rotateZ(45deg);
    -o-transform: rotateZ(45deg);
    -ms-transform: rotateZ(45deg);
    -webkit-transform: rotateZ(45deg);
    -moz-transform: rotateZ(45deg);
}
.cssload-thecube .cssload-cube {
    position: relative;
    transform: rotateZ(45deg);
    -o-transform: rotateZ(45deg);
    -ms-transform: rotateZ(45deg);
    -webkit-transform: rotateZ(45deg);
    -moz-transform: rotateZ(45deg);
}
.cssload-thecube .cssload-cube {
    float: left;
    width: 50%;
    height: 50%;
    position: relative;
    transform: scale(1.1);
    -o-transform: scale(1.1);
    -ms-transform: scale(1.1);
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
}
.cssload-thecube .cssload-cube:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(200, 172, 102);
    animation: cssload-fold-thecube 2.76s infinite linear both;
    -o-animation: cssload-fold-thecube 2.76s infinite linear both;
    -ms-animation: cssload-fold-thecube 2.76s infinite linear both;
    -webkit-animation: cssload-fold-thecube 2.76s infinite linear both;
    -moz-animation: cssload-fold-thecube 2.76s infinite linear both;
    transform-origin: 100% 100%;
    -o-transform-origin: 100% 100%;
    -ms-transform-origin: 100% 100%;
    -webkit-transform-origin: 100% 100%;
    -moz-transform-origin: 100% 100%;
}
.cssload-thecube .cssload-c2 {
    transform: scale(1.1) rotateZ(90deg);
    -o-transform: scale(1.1) rotateZ(90deg);
    -ms-transform: scale(1.1) rotateZ(90deg);
    -webkit-transform: scale(1.1) rotateZ(90deg);
    -moz-transform: scale(1.1) rotateZ(90deg);
}
.cssload-thecube .cssload-c3 {
    transform: scale(1.1) rotateZ(180deg);
    -o-transform: scale(1.1) rotateZ(180deg);
    -ms-transform: scale(1.1) rotateZ(180deg);
    -webkit-transform: scale(1.1) rotateZ(180deg);
    -moz-transform: scale(1.1) rotateZ(180deg);
}
.cssload-thecube .cssload-c4 {
    transform: scale(1.1) rotateZ(270deg);
    -o-transform: scale(1.1) rotateZ(270deg);
    -ms-transform: scale(1.1) rotateZ(270deg);
    -webkit-transform: scale(1.1) rotateZ(270deg);
    -moz-transform: scale(1.1) rotateZ(270deg);
}
.cssload-thecube .cssload-c2:before {
    animation-delay: 0.35s;
    -o-animation-delay: 0.35s;
    -ms-animation-delay: 0.35s;
    -webkit-animation-delay: 0.35s;
    -moz-animation-delay: 0.35s;
}
.cssload-thecube .cssload-c3:before {
    animation-delay: 0.69s;
    -o-animation-delay: 0.69s;
    -ms-animation-delay: 0.69s;
    -webkit-animation-delay: 0.69s;
    -moz-animation-delay: 0.69s;
}
.cssload-thecube .cssload-c4:before {
    animation-delay: 1.04s;
    -o-animation-delay: 1.04s;
    -ms-animation-delay: 1.04s;
    -webkit-animation-delay: 1.04s;
    -moz-animation-delay: 1.04s;
}

@keyframes cssload-fold-thecube {
    0%,
    10% {
        transform: perspective(136px) rotateX(-180deg);
        opacity: 0;
    }
    25%,
    75% {
        transform: perspective(136px) rotateX(0deg);
        opacity: 1;
    }
    90%,
    100% {
        transform: perspective(136px) rotateY(180deg);
        opacity: 0;
    }
}

// Preloader

.navover {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($color-sec, 0.8);
    z-index: 999;
    opacity: 0;
    visibility: hidden;
    transition: 0.5s;

    &.open {
        opacity: 1;
        visibility: visible;
    }
}

.sideNav {
    padding: 24px;
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: $color-white;
    z-index: 9999;
    overflow-x: hidden;
    overflow-y: hidden;
    transition: 0.5s;
    transform: scale(0);
    transform-origin: top right;

    html[dir="ltr"] & {
        transform-origin: top left;
    }

    &.open {
        transform: scale(1);
        transform-origin: top left;

        html[dir="ltr"] & {
            transform-origin: top right;
        }
    }

    .opt {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px;

        .lang {
            a {
                color: $color-sec;
                font-size: 15px;
            }
        }

        .close1 {
            font-size: 20px;
            color: $color-sec;
            cursor: pointer;
            display: flex;
            align-items: center;
        }
    }

    .links {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        .list {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            height: 100%;
            overflow: auto;
            text-align: center;

            & > li {
                padding: 7px 15px;
                width: 100%;

                a {
                    width: 100%;
                    display: block;
                    font-size: 16px;
                    color: $color-sec;
                    // line-height: 1.6;
                    transition: 0.5s;

                    &.lnr {
                        font-family: "sa";
                        position: relative;
                        width: 125px;
                        html[dir="ltr"] & {
                            margin: auto;
                            width: 170px;
                        }
                        &:before {
                            position: absolute;
                            top: 50%;
                            left: 23px;
                            transform: translateY(-15%);
                            font-family: "Linearicons-Free" !important;
                            color: $color-sec;
                            font-size: 8px;
                            transition: 0.5s;
                            html[dir="ltr"] & {
                                right: 23px;
                                left: unset;
                            }
                        }

                        + .dropDown {
                            display: none;
                            padding-top: 10px;

                            li {
                                &:not(:last-child) {
                                    margin-bottom: 10px;
                                }

                                a {
                                    color: #898989;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

// Side Nav

.searchh {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(64, 64, 66, 0.98);
    @include Flex;
    z-index: 9999;
    opacity: 0;
    visibility: hidden;
    transition: 0.5s;

    &:after {
        content: "";
        width: 100%;
        height: 30%;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        border: 3px solid $color-prim;
        border-right: 0;
        border-left: 0;
        transition: 0.5s 0.5s;
    }

    &:before {
        content: "";
        width: 30%;
        height: 100%;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 0;
        border: 3px solid $color-prim;
        border-top: 0;
        border-bottom: 0;
        transition: 0.5s 0.5s;
    }

    form {
        position: relative;
        width: 50%;
        z-index: 9;
        transform: scale(1.4);
        opacity: 0;
        visibility: hidden;
        transition: 0.5s 0.5s;

        @include Xs {
            width: 90%;
            transform: scale(1.4) translateY(-115px);
        }

        > span {
            position: absolute;
            top: -140px;
            left: 50%;
            transform: translateX(-50%);
            color: #fff;
            font-size: 25px;

            @include Xs {
                top: -90px;
            }
        }

        input {
            width: 100%;
            height: 50px;
            border: 0;
            padding: 0 25px;
            background-color: rgba(255, 255, 255, 0.9);
        }

        button {
            position: absolute;
            top: 0;
            left: 0;
            html[dir="ltr"] & {
                right: 0;
                left: unset;
            }
            height: 100%;
            width: 50px;
            background-color: $color-prim;
            border: 0;
            color: #fff;
            font-size: 25px;
            cursor: pointer;
            transition: 0.5s;

            &:hover {
                background-color: rgba($color-prim, 0.8);
            }
        }
    }

    &.open {
        opacity: 1;
        visibility: visible;

        form {
            opacity: 1;
            visibility: visible;
            transform: scale(1) translateY(-115px);
        }

        &:after {
            height: 100%;
        }

        &:before {
            width: 100%;
        }
    }
}

// Start Header
header {
    height: 100vh;
    position: relative;
    overflow: hidden;
    background-size: cover;
    background-attachment: fixed;

    @include mob480 {
        height: 65vh;
    }

    > img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
    }

    .logo {
        img {
            @include Md {
                height: 100px;
            }

            @include Lg {
                height: auto;
            }

            @include mob375 {
                height: 85px;
            }
        }

        .menuTriger,
        .searchTriger {
            display: none;

            span {
                font-size: 25px;
                color: $color-white;
            }

            @include Sm {
                display: flex;
                position: absolute;
                top: 50%;
                left: 0;
                html[dir="ltr"] & {
                    left: unset;
                    right: 0;
                }
            }
        }

        .menuTriger {
            span {
                font-size: 28px;
            }
        }

        .searchTriger {
            @include Sm {
                left: 40px;
                html[dir="ltr"] & {
                    left: unset;
                    right: 40px;
                }
                margin-top: 2px;
            }
        }

        @include Sm {
            width: 100%;
            position: relative;
        }
    }

    nav {
        padding: 10px 0;

        > .container {
            position: relative;
        }

        .lang {
            position: absolute;
            top: 5px;
            left: 0;
            html[dir="ltr"] & {
                left: unset;
                right: 0;
            }
            font-size: 14px;
            color: #c3c3c3;
            font-family: "sa";
            line-height: 1;
            transition: 0.5s;

            @include Sm {
                display: none;
            }

            &:hover {
                color: $color-white;
            }
        }

        .content {
            @include Flex;
            justify-content: space-between;

            .links {
                margin-top: 35px;
                position: relative;

                @include Sm {
                    display: none;
                }

                ul {
                    @include Flex;

                    li {
                        position: relative;
                        display: flex;
                        align-items: center;
                        &:not(:last-child) {
                            margin-left: 30px;
                            margin-top: -5px;
                            html[dir="ltr"] & {
                                margin-left: 0;
                                margin-right: 30px;
                            }
                        }

                        a {
                            font-size: 18px;
                            color: #fff;
                            font-family: "sa";
                            transition: 0.5s;

                            html[dir="ltr"] & {

                                @include Md{
                                    font-size: 14px
                                }
                                @include Lg{
                                    font-size: 18px
                                }
                            }

                            &.lnr {
                                &:before {
                                    position: absolute;
                                    top: 50%;
                                    left: -12px;
                                    transform: translateY(-15%);
                                    font-family: "Linearicons-Free" !important;
                                    color: #fff;
                                    font-size: 8px;
                                    transition: 0.5s;
                                    html[dir="ltr"] & {
                                        right: -12px;
                                        left: unset;
                                    }
                                }
                            }

                            &:hover {
                                color: $color-prim;

                                &:before {
                                    color: $color-prim;
                                }
                            }
                        }

                        .dropDown {
                            position: absolute;
                            right: 0;
                            top: 200%;
                            width: 160px;
                            background-color: transparent;
                            padding: 7px 10px;
                            opacity: 0;
                            visibility: hidden;
                            z-index: 9;
                            display: flex;
                            flex-direction: column;
                            transform: translateX(20px);
                            border-radius: 4px;
                            transition: .3s cubic-bezier(1, 0, 0, 1);

                            html[dir="ltr"] & {
                                left: 0;
                                right: unset;
                                width: 170px;
                                transform: translateX(-20px);
                            }

                            // &:after {
                            //     content: "";
                            //     width: 0;
                            //     height: 0;
                            //     display: block;
                            //     border-width: 6px;
                            //     border-color: transparent transparent
                            //         $color-white;
                            //     border-style: solid;
                            //     bottom: 100%;
                            //     position: absolute;
                            //     right: 45px;
                            //     html[dir="ltr"] & {
                            //         right: unset;
                            //         left: 45px;
                            //     }
                            // }

                            li {
                                margin: 0 !important;
                                width: 100%;
                                // opacity: 0;
                                // transition: 0.7s;

                                &:not(:last-child) {
                                    border-bottom: 1px solid #e6e6e6;
                                }

                                a {
                                    line-height: 2.4;
                                    font-size: 15px;
                                    font-family: "sa";
                                    display: block;
                                    color: $color-sec;
                                    width: 100%;

                                    &:hover {
                                        color: $color-prim;
                                    }

                                    html[dir="ltr"] & {
                                        font-size: 14px;
                                    }

                                    &:after {
                                        display: none;
                                    }
                                }
                            }
                        }

                        &:hover {
                            .dropDown {
                                opacity: 1;
                                visibility: visible;
                            }
                        }

                        span.lnr-magnifier {
                            cursor: pointer;
                            font-size: 24px;
                            color: #fff;
                            margin-right: 20px;
                            html[dir="ltr"] & {
                                margin-right: 0;
                                margin-left: 20px;
                            }
                        }
                    }
                }
            }
        }
    }

    > .content {
        position: absolute;
        top: 50%;
        transform: translateY(-30%);
        right: 0;
        width: 100%;

        .container > p{
            @include Xs{
                display: none;
            }
        }

        p {
            font-size: 24px;
            font-family: "sa";
            color: #fff;

            @include Sm {
                text-align: center;
            }
        }

        h2 {
            width: 61%;
            font-size: 55px;
            font-family: "saBold";
            color: #fff;
            line-height: 1.182;
            margin-bottom: 55px;

            @include Sm {
                width: 100%;
                text-align: center;
                font-size: 40px;
            }

            @include mob480 {
                font-size: 25px;
                margin-bottom: 20px;
            }
        }

        a {
            @include Flex;
            width: 200px;
            height: 55px;
            background-color: #c8ac66;
            color: #fff;
            transition: 0.5s;

            @include Sm {
                margin: auto;
            }

            p {
                font-size: 16px;
                font-family: "sa";
                color: #fff;
                margin-bottom: 5px;
            }

            span {
                font-size: 18px;
                color: currentColor;
                margin-right: 14px;
                margin-top: 3px;
                html[dir="ltr"] & {
                    margin-right: 0;
                    margin-left: 14px;
                }
            }

            &:hover {
                transform: translateY(3px);
            }
        }
    }

    .social {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 60px;
        html[dir="ltr"] & {
            left: unset;
            right: 60px;
        }
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        @include Sm {
            display: none;
        }

        .shape {
            height: 85px;
            width: 1px;
            background-color: #b6b6b7;
        }

        a {
            margin-top: 25px;

            i {
                color: rgba($color-white, 0.55);
                font-size: 18px;
                transition: 0.5s;

                &:hover {
                    color: #fff;
                }
            }
        }
    }
}
// End Header
.dropDown-bg {
    transition: .3s cubic-bezier(1, 0, 0, 1);
    // border-bottom: 2px solid white;
    position: absolute;
    top: 160%;
    background: white;
    z-index: -1;
    opacity: 0;
    visibility: hidden;
    // right: 0;
    // top: 200%;
    // border-radius: 20px;
    display: block;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);

    &:after {
        content: "";
        width: 0;
        height: 0;
        display: block;
        border-width: 6px;
        border-color: transparent transparent
            $color-white;
        border-style: solid;
        bottom: 100%;
        position: absolute;
        right: 45px;
        html[dir="ltr"] & {
            right: unset;
            left: 45px;
        }
    }

    &.open {
        opacity: 1;
        visibility: visible;
    }
}

