}

// Projects

.projects {
    padding: 100px 0 130px;
    background-color: #f6f6f6;

    @include Sm {
        padding: 55px 0 65px;
    }

    .head {
        @include Flex;
        justify-content: space-between;
        margin-bottom: 40px;

        @include mob480 {
            justify-content: center;
            margin-bottom: 50px;
        }

        p {
            margin: 0;
            font-size: 30px;
            font-family: "saMd";
            color: $color-sec;

            @include mob480 {
                font-size: 25px;
                margin-left: 25px;

                html[dir="ltr"] & {
                    margin-right: 25px;
                    margin-left: 0;
                }
            }
        }

        a {
            font-size: 20px;
            color: $color-sec;
            transition: 0.5s;

            &:hover {
                color: $color-prim;
            }

            @include mob480 {
                font-size: 18px;
            }
        }
    }

    .projectss {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-start;

        .project {
            width: calc((100% - 60px) / 3);
            position: relative;

            @include Sm {
                width: 100%;
            }

            .overlay {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(200, 172, 102, 0.95);
                @include Flex;
                opacity: 0;
                visibility: hidden;
                transition: 0.5s;
                perspective: none;

                a {
                    @include Flex;
                    align-items: unset;
                    width: 135px;
                    height: 40px;
                    // border: 1px solid #fff;
                    outline: 1px solid #fff;
                    transition: 0.5s;
                    transform: scale(0.8) translateZ(0);
                    opacity: 0;
                    backface-visibility: hidden;

                    span {
                        font-size: 16px;
                        font-family: "sa";
                        color: #fff;
                        line-height: 31px;
                        transition: 0.5s;
                    }

                    &:hover {
                        background-color: #fff;
                        span {
                            color: $color-sec;
                        }
                    }
                }
            }

            &:hover {
                .overlay {
                    opacity: 1;
                    visibility: visible;

                    a {
                        opacity: 1;
                        transform: scale(1) rotate(0);
                    }
                }
            }

            &:nth-child(4),
            &:nth-child(6) {
                @include Md {
                    margin-top: -85px;
                }

                @include Lg {
                    margin-top: -111px;
                }
            }

            &:nth-child(5) {
                margin-top: 36px;
            }

            &:nth-child(7),
            &:nth-child(8) {
                @include Md {
                    margin-top: -204px;
                }

                @include Lg {
                    margin-top: -260px;
                }
            }
        }
    }

    .owl-stage {
        display: flex;
        align-items: center;
    }

    .owl-dots {
        width: 100%;
        @include Flex;
        margin-top: 50px;

        @include mob480 {
            margin-top: 30px;
        }

        button {
            &:not(:last-child) {
                margin-left: 5px;
            }

            span {
                display: flex;
                width: 8px;
                height: 8px;
                background-color: $color-sec;
                border-radius: 50%;
            }

            &.active {
                span {
                    background-color: $color-prim;
                }
            }
        }
    }
}

