// footer

footer {
    padding: 65px 0 50px;
    background-image: url("../images/footer-bg.jpg");
    background-size: cover;
    background-position: bottom;
    background-attachment: fixed;
    background-repeat: no-repeat;
    // background-position-x: 0;
    // background-position-y: 277px;
    // background-color: rgb(64, 64, 66);

    @include Sm {
        padding: 50px 0;

        background-size: 100%;
        background-repeat: no-repeat;
        background-color: #404042;
        background-attachment: unset;
    }

    .contact {
        padding: 0 0 70px;
        display: flex;
        align-items: center;
        border-bottom: 1px solid rgba(#fff, 0.1);
        margin-bottom: 70px;

        @include Sm {
            flex-direction: column;
            align-items: flex-start;
            position: relative;
            z-index: 9;
            margin-bottom: 30px;
            padding: 20px 0 70px;
        }

        > div {
            display: flex;
            align-items: center;

            @include mob480 {
                flex-direction: column;
                align-items: center;
                justify-content: center;
                width: 100%;
            }

            &.right {
                margin-left: 110px;
                html[dir="ltr"] & {
                    margin-left: 0;
                    margin-right: 110px;
                }

                @include Sm {
                    margin: 0;
                    margin-bottom: 25px;
                }
            }
        }

        .icon {
            width: 80px;
            height: 80px;
            border-radius: 50%;
            background-color: rgba(#c8ac66, 0.2);
            @include Flex;
            margin-left: 23px;
            html[dir="ltr"] & {
                margin-right: 23px;
                margin-left: 0;
            }

            @include mob480 {
                margin: 0 !important;
                margin-bottom: 10px;
            }

            span {
                font-size: 40px;
                color: $color-prim;

