        @include Sm {
            margin-bottom: 15px;
        }

        .collapse {
            display: block;

            @include Sm {
                display: none;
            }

            &.in {
                display: block;
            }
        }

        & > button {
            font-size: 18px;
            font-family: "saBold";
            color: #fff;
            margin-bottom: 35px;
            background-color: transparent;
            border: 0;
            padding: 0;
            pointer-events: none;

            &.trans {
                &:after {
                    content: "\2212";
                }
            }

            @include Sm {
                width: 100%;
                margin: auto;
                pointer-events: all;
                margin-bottom: 0;
                display: flex;
                align-items: center;
                justify-content: center;
