            font-family: "saBold";
            color: #fff;
            margin-bottom: 35px;
        }

        & > span {
            font-size: 16px;
            font-family: "sa";
            color: #fff;
            margin-bottom: 23px;
            display: block;
        }
    }

    .frm {
        position: relative;
        margin-bottom: 17px;
        width: 360px;

        @include Md {
            margin: auto;
            width: 100%;
        }

        @include Lg {
            width: 360px;
        }

        @include Sm {
            margin: auto;
        }

        @include mob375 {
            width: 280px;
        }

        input {
            padding: 0 20px;
            height: 45px;
            width: 100%;
            border: 0;
            color: #a8a8a8;
            font-size: 14px;
            background-color: #fff;
            transition: 0.5s;

            &::placeholder {
                color: #a8a8a8;
            }
        }

        button {
            position: absolute;
            top: 50%;
            left: 0px;
            html[dir="ltr"] & {
                left: unset;
                right: 0px;
            }
            transform: translateY(-51%);
            width: 45px;
            height: 45px;
            background-color: #c8ac66;
            border: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            border-top: 1px solid #c8ac66;
            transition: 0.5s;

            span {
                font-size: 16px;
                color: #fff;
            }
        }
    }

    .socialP {
        margin-bottom: 35px;
        font-size: 18px;
        font-family: "saBold";
        color: #fff;

        @include Sm {
            text-align: center;
            margin-top: 45px;
