.searchh {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(64, 64, 66, 0.98);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;
}

.searchh:after {
  content: "";
  width: 100%;
  height: 30%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  border: 3px solid #C8AC66;
  border-right: 0;
  border-left: 0;
  transition: 0.5s 0.5s;
}

.searchh:before {
  content: "";
  width: 30%;
  height: 100%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 0;
  border: 3px solid #C8AC66;
  border-top: 0;
  border-bottom: 0;
  transition: 0.5s 0.5s;
}

.searchh form {
  position: relative;
  width: 50%;
  z-index: 9;
  transform: scale(1.4);
  opacity: 0;
  visibility: hidden;
  transition: 0.5s 0.5s;
}

@media (max-width: 767px) {
  .searchh form {
    width: 90%;
    transform: scale(1.4) translateY(-115px);
  }
}

.searchh form > span {
  position: absolute;
  top: -140px;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
  font-size: 25px;
}

@media (max-width: 767px) {
  .searchh form > span {
    top: -90px;
  }
}

.searchh form input {
  width: 100%;
  height: 50px;
  border: 0;
  padding: 0 25px;
  background-color: rgba(255, 255, 255, 0.9);
}

.searchh form button {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 50px;
  background-color: #C8AC66;
  border: 0;
  color: #fff;
  font-size: 25px;
  cursor: pointer;
  transition: 0.5s;
}

html[dir="ltr"] .searchh form button {
  right: 0;
  left: unset;
}

.searchh form button:hover {
  background-color: rgba(200, 172, 102, 0.8);
}

.searchh.open {
  opacity: 1;
  visibility: visible;
}

.searchh.open form {
  opacity: 1;
  visibility: visible;
  transform: scale(1) translateY(-115px);
}

.searchh.open:after {
  height: 100%;
}

.searchh.open:before {
  width: 100%;
}

